<template>
  <div id="app">
    <div class="nav">
      <div class="logo">
        <img :src="require(`@/assets/logo.png`)" alt />
      </div>
      <ul>
        <li
          v-for="(nav, index) in navs"
          :class="{'active':curNavIndex===index}"
          @click="changePage(index)"
        >
          <img :src="changeNavImg(index,nav)" alt />
        </li>
      </ul>
    </div>
    <div class="main">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      curNavIndex: 0,
      navs: [
        {
          normal: require(`@/assets/nav_1.png`),
          active: require(`@/assets/nav_1_active.png`)
        },
        {
          normal: require(`@/assets/nav_2.png`),
          active: require(`@/assets/nav_2_active.png`)
        },
        {
          normal: require(`@/assets/nav_3.png`),
          active: require(`@/assets/nav_3_active.png`)
        },
        {
          normal: require(`@/assets/nav_4.png`),
          active: require(`@/assets/nav_4_active.png`)
        }
      ]
    };
  },
  methods: {
    // 
    changeNavImg(index, nav) {
      if (this.curNavIndex === index) {
        return nav.active;
      } else {
        return nav.normal;
      }
    },
    // 
    changePage(index) {
      this.curNavIndex = index;
      let pageName = "index";
      switch (index) {
        case 0:
          pageName = "index";
          break;
        case 1:
          pageName = "intro";
          break;
        case 2:
          pageName = "pic";
          break;
        case 3:
          pageName = "web";
          break;
        default:
          break;
      }

      this.$router.push({ name: pageName });
    }
  },
  watch: {
    $route(to, from) {
      switch (to.name) {
        case "index":
          this.curNavIndex = 0;
          break;
        case "intro":
          this.curNavIndex = 1;
          break;
        case "pic":
          this.curNavIndex = 2;
          break;
        case "web":
          this.curNavIndex = 3;
          break;
        default:
          break;
      }
    }
  }
};
</script>

<style lang="less">
@import "./common/less/index.less";

@time: 300ms;

.delay_1 {
  animation-delay: @time;
}
.delay_2 {
  animation-delay: @time*2;
}
.delay_3 {
  animation-delay: @time*3;
}
@nav_width: 70px;

.nav {
  position: fixed;
  left: 0;
  top: 0;
  width: @nav_width;
  height: 100vh;
  background: #2a2a2a;
  z-index: 2;

  ul {
    height: calc(100vh - @nav_width);

    li {
      text-align: center;
      padding: 30px 0;
      cursor: pointer;
      transition: all 0.5s;

      &:hover,
      &:active {
        background: #000;
      }

      img {
        vertical-align: bottom;
        width: 35px;
      }
    }
  }
}
.logo {
  background: @theme_color_1;
  width: @nav_width;
  height: @nav_width;
  margin-bottom: 30px;
  text-align: center;

  img {
    width: 90%;
    vertical-align: bottom;
  }
}
.main {
  width: calc(100vw - @nav_width);
  margin-left: @nav_width;
}
@media screen and (max-width: 768px) {
  @nav_width: 40px;

  .nav {
    width: @nav_width;

    ul {
      li {
        img {
          width: 25px;
        }
      }
    }
  }
  .logo {
    width: @nav_width;
    height: @nav_width;
  }
  .main {
    width: calc(100vw - @nav_width);
    margin-left: @nav_width;
  }
}
</style>



